import axios from 'axios';
import React from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { useState } from 'react/cjs/react.development';

const Create = (props) => {
  const history = useHistory();
  const params = useParams();
  const tablename = params.table;
  const [name, setName] = useState('');
  const [price, setPrice] = useState('');
  const [content, setContent] = useState('');

  const onaddtitle = (event) => {
    setName(event.target.value);
  };

  const onaddprice = (event) => {
    setPrice(event.target.value);
  };

  const onaddcontent = (event) => {
    setContent(event.target.value);
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (window.confirm(' ?') === true) {
      let body = {
        name: name,
        price: price,
        content: content,
        tablename: tablename,
      };
      axios.post('/api/insert', body).then((res) => {
        props.history.push('/');
      });
    } else {
      return;
    }
  };

  return (
    <>
      <form className="input-menu" onSubmit={onSubmit}>
        <div className="inputdiv">
          <h2 className="title-input"></h2>
          <input
            type="text"
            className="inputbox inputbox-text"
            placeholder="  "
            onChange={onaddtitle}
          />
          <h2 className="title-input"></h2>
          <input
            type="number"
            className="inputbox inputbox-num"
            placeholder="  "
            onChange={onaddprice}
          />
          <h2 className="title-input"></h2>
          <input
            type="text"
            className="inputbox inputbox-con"
            placeholder="  "
            onChange={onaddcontent}
          />
          <button type="submit" className="btn">
            
          </button>
        </div>
      </form>
    </>
  );
};

export default Create;
